@import url(https://fonts.googleapis.com/css?family=Noto+Sans+KR:400,700&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}

body {
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 16px;
  background-color: #f8f9fa;
  color: #333333;
}

header {
  min-width: 1280px;
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  background-color: white;
  font-size: 18px;
}

#menuButton {
  margin: 0 0 0 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  -webkit-transition: all 0.15s linear;
  transition: all 0.15s linear;
  width: 100px;
  height: 60px;
}

#menuButton a {
  color: #333;
  text-decoration: none;
  -webkit-transition: inherit;
  transition: inherit;
  display: inline-block;
  width: 100px;
  height: 60px;
  line-height: 60px;
  text-align: center;
}

#menuButton:hover a {
  color: #0077ff;
}

#menuButton:hover span:first-child {
  background-color: #0077ff;
}

#menuButton span:first-child {
  -webkit-transition: inherit;
  transition: inherit;
  background-color: #333;
  display: inline-block;
  width: 18px;
  height: 16px;
  background-position: 0 0;
  background-repeat: no-repeat;
  margin-right: 8px;
}

header img {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}

#registerLogin {
  font-size: 18px;
  margin: 0 40px 0;
}

#registerLogin li {
  display: inline-block;
  padding: 7px 14px;
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
}

#registerLogin li:first-child:hover a {
  color: #0077ff;
}

#registerLogin li:last-child:hover {
  border-color: #0077ff;
  background-color: #0077ff;
}

#registerLogin li:last-child:hover a {
  color: white;
}

#registerLogin li:nth-child(2n) {
  font-weight: bold;
  border: 2px solid #333;
  border-radius: 25px;
  margin: 0 0 0 4px;
}
#registerLogin a {
  -webkit-transition: inherit;
  transition: inherit;
  text-decoration: none;
  color: #333;
}

#slideShowWrap {
  min-width: 1280px;
  height: 500px;
  position: relative;
}

#slideShow {
  width: 100%;
  height: 500px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

#slideShowWrap ul {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  display: flex;
  justify-content: center;
}

#slideShowWrap li {
  opacity: 0.7;
  background-color: rgba(255, 255, 255, 0.2);
  width: 250px;
  height: 80px;
  color: white;
  font-weight: bold;
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
  cursor: pointer;
}
#slideShowWrap li:hover {
  opacity: 1;
}

#slideShowWrap .sub {
  font-size: 13px;
  margin: 14px 12px;
  color: rgba(255, 255, 255, 0.7);
}

#slideShowWrap .programName {
  font-size: 18px;
  margin: 5px 12px;
}

#slideShowWrap li + li {
  margin: 0 0 0 2px;
}

#mainWrap {
  width: 1280px;
  margin: 0 auto;
}

#bestProgramWrap {
  margin: 75px 0;
  position: relative;
}

#bestProgramWrap h2 {
  font-size: 24px;
  font-weight: bold;
}

#bestProgramWrap ul {
  margin-top: 30px;
}

#bestProgramWrap li {
  display: inline-block;
  width: 150px;
  height: 350px;
  background-color: white;
}

#bestProgramWrap li:hover {
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
  box-shadow: 0 0 12px 5px rgba(0, 0, 0, 0.1);
}

#bestProgramWrap li:hover img {
  -webkit-transition: all 0.3s cubic-bezier(0.23, 1, 0.32, 1);
  transition: all 0.3s cubic-bezier(0.23, 1, 0.32, 1);
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

#bestProgramWrap .programImg {
  overflow: hidden;
  height: 200px;
}

#bestProgramWrap .tag {
  margin: 16px 12px 12px;
  font-size: 10px;
  font-weight: bold;
  color: white;
  background-color: #00dbd3;
  width: 32px;
  height: 16px;
  border-radius: 8px;
  text-align: center;
  line-height: 16px;
}

#bestProgramWrap .programName {
  font-size: 14px;
  font-weight: bold;
  margin-left: 12px;
}

#bestProgramWrap .sub {
  font-size: 12px;
  color: #999999;
  position: absolute;
  bottom: 12px;
  margin-left: 12px;
}

#bestProgramWrap li + li {
  margin-left: 8px;
}

#bestProgramWrap .eventBanner {
  width: 465px;
  height: 350px;
  background: white;
  position: absolute;
  top: 54px;
  right: 0;
}

#findProgramWrap {
  position: relative;
}

#findProgramWrap h2 {
  font-size: 24px;
  font-weight: bold;
}

#tagWrap {
  height: 60px;
  background-color: white;
  padding: 20px 30px;
  margin-top: 30px;
}

#tagWrap li {
  font-size: 14px;
  display: inline-block;
  padding: 5px 10px;
  color: #666;
  cursor: pointer;
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
}

#tagWrap li:hover {
  color: #0077ff;
}

#tagWrap li.selected {
  color: white;
  background-color: #333;
  padding: 5px 8px;
  border-radius: 20px;
}

#programSearch {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 12px;
  color: #666;
  border: none;
  border-bottom: 1px solid #999999;
  background-color: transparent;
  width: 300px;
  padding: 8px;
}

#programSearchButton {
  position: absolute;
  top: 8px;
  right: 16px;
  width: 13px;
  height: 13px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

#programCardWrap {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: wrap;
}

.programCard {
  width: 300px;
  height: 320px;
  background-color: white;
  margin: 20px 0;
}

.programCard:hover {
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
  box-shadow: 0 0 12px 5px rgba(0, 0, 0, 0.1);
}

.programCard:hover img {
  -webkit-transition: all 0.3s cubic-bezier(0.23, 1, 0.32, 1);
  transition: all 0.3s cubic-bezier(0.23, 1, 0.32, 1);
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.programCard .programImg {
  height: 200px;
  overflow: hidden;
}

.programCard .programDetails {
  padding: 20px 16px 0;
}

.programCard .programTitle {
  font-size: 16px;
  font-weight: bold;
}

.programCard .programSub {
  font-size: 12px;
  color: #999;
  margin-top: 10px;
}

.programCard .programTime {
  font-size: 12px;
  color: #999;
  padding: 10px 0;
  border-top: 1px solid #eee;
  margin-top: 30px;
}

#programCardAddBtn {
  font-weight: bold;
  width: 170px;
  height: 50px;
  border: 2px solid #999;
  color: #999999;
  font-size: 14px;
  text-align: center;
  line-height: 46px;
  margin: 50px auto;
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
  cursor: pointer;
}

#programCardAddBtn:hover {
  background-color: #999;
  color: white;
}

